@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
.App{border-width:4px;border-style:solid;border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff;background-color:#c2c2c2;padding:16px}.Header{border-width:4px;border-style:solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b;align-items:center;background-color:#c0c0c0;display:flex;justify-content:space-between;padding:10px 20px}.Body{border-width:4px;border-style:solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b;display:grid;grid-template-columns:repeat(9, 1fr);grid-template-rows:repeat(9, 1fr);margin-top:16px}.Face{border-width:4px;border-style:solid;border-right-color:#7b7b7b;border-bottom-color:#7b7b7b;border-left-color:#fff;border-top-color:#fff;align-items:center;cursor:pointer;display:flex;font-size:30px;height:52px;justify-content:center;outline:none;width:52px}.Face:active{border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b}.Face:disabled{border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff}.Face.focus{outline:none}

.NumberDisplay{width:80px;height:48px;color:#ff0701;background-color:#000;font-size:30px;display:flex;justify-content:center;align-items:center}

.Button{border-width:4px;border-style:solid;border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff;width:30px;height:30px;cursor:pointer;display:flex;justify-content:center;align-items:center;outline:none}.Button:active{border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b}.Button:disabled{border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff}.Button.focus{outline:none}.Button.visible{border-color:#7b7b7b;border-width:1px}.Button.red{background-color:red}.Button span{font-size:18px}.Button.value-1{color:blue}.Button.value-2{color:green}.Button.value-3{color:red}.Button.value-4{color:purple}.Button.value-5{color:maroon}.Button.value-6{color:turquoise}.Button.value-7{color:black}.Button.value-8{color:gray}

.Text{text-align:center;font-size:16px}.Text.green{color:green}.Text.red{color:red}

*{box-sizing:border-box}body{margin:0;padding-top:60px;display:flex;justify-content:center;align-items:center;font-family:'Black Ops One', cursive}

